.slider {
  max-width: 90%;
  position: relative;
  margin: auto;
}

/* Картинка масштабируется по отношению к родительскому элементу */
.slider .slider__item img {
  object-fit: cover;
  width: 100%;
  height: 700px;
}

/* Кнопки вперед и назад */
.slider__left {
  cursor: pointer;
  position: absolute;
  left: 30px;
  top: 50%;
  width: auto;
  height: auto;
  margin-top: -22px;
  padding: 16px 10px 16px 10px;
  font-weight: bold;
  font-size: 16px;
  transition: 0.6s ease;
  border: 1px solid #FFFFFF;
  background: transparent;
  backdrop-filter: blur(2.5px);
  border-radius: 50%;
}

.slider__rigth {
  cursor: pointer;
  position: absolute;
  right: 30px;
  top: 50%;
  width: auto;
  height: auto;
  margin-top: -22px;
  padding: 16px 10px 16px 10px;
  font-weight: bold;
  font-size: 16px;
  transition: 0.6s ease;
  border: 1px solid #FFFFFF;
  background: transparent;
  backdrop-filter: blur(2.5px);
  border-radius: 50%;
}

/* Анимация слайдов */
.slider .slider__item {
  animation-name: fade;
  animation-duration: 1.5s;
}

@keyframes fade {
  from {
    opacity: 0.4;
  }
  to {
    opacity: 1;
  }
}
